@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-wrapper{margin:0;display:grid;grid-template-areas:"n m m m m m m m m m m m"}.main-wrapper .left-nav{grid-area:n;background-color:#f4f5f7;height:100vh;min-width:13rem}.main-wrapper .left-nav h2{padding:0.4rem;margin-top:2rem}.main-wrapper .left-nav ul{list-style:none;margin-top:1rem;padding:0}.main-wrapper .left-nav ul>li{padding:1rem;font-size:1.3rem;padding-left:2rem;cursor:pointer}.main-wrapper .left-nav li.active{color:#0052cc;background-color:#ebecf0}.main-section{grid-area:m}.main-section input[type="text"]{border-radius:.2rem;width:13rem;box-sizing:border-box;border:2px solid #ccc;font-size:14px;background-color:#f4f5f7;padding:10px 15px 10px 15px;margin-top:3rem;margin-left:5.5rem;transition:width 0.4s ease-in-out}.main-section input[type="text"]:focus{width:14rem}.main-section span.material-icons{position:relative;left:-30px;top:6px}.grid-container{border-radius:.2rem;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1rem;padding:1px 50px 0;margin:2rem 2rem}.grid-items{background-color:#f4f5f7;padding:0.5rem}.draggable{border-radius:.2rem;padding:1rem;background-color:white;cursor:move;min-height:160px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:1rem}.draggable.dragging{opacity:0.5}.draggable .footer{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.draggable .footer .left{background-color:cyan;border:1px solid cyan;border-radius:.2rem;padding:0.2rem 0.9rem;font-size:0.8rem;-webkit-align-self:center;align-self:center}.draggable .footer .right{background-color:rebeccapurple;border:1px solid rebeccapurple;border-radius:0.1rem;padding:0.1rem 0.9rem;color:white}.grid-items:nth-child(1),.grid-items:nth-child(2),.grid-items:nth-child(3),.grid-items:nth-child(4){padding:0.8rem;padding-left:1.2rem}

